@mixin bg($val, $bg... ) {
    &:nth-of-type(#{$val}) {
        background: $bg;
    }
}

#home {
    .home-ul {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        position: relative;
        display: flex;
        justify-content: flex-end;
        .home-login{
        	margin-right: 2vw;
        	margin-top: 1vh;
        }

        ul {
        	position: absolute;
        	width: 100%;
        	left: 0;
        	top: 0;
        	z-index: -1;

            li {
                height: 100vh;
                @include bg(2, url(../assets/image/bg1.jpg) no-repeat center);
                @include bg(1, url(../assets/image/pic.png) no-repeat center);
                @include bg(3, #f06292);
                font-size: 40px;
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
		ol{
			position: absolute;
			right: 2vw;
			top: 50%;
			margin-top: -25px;
			li{
				width: 10px;
				height: 10px;
				background: #fff;
				border-radius: 50%;
				&:not(:last-child){
					margin-bottom: 10px;
				}
				&.current{
					background: #12e386;
				}
			}
		}
    }
}